<template>
  <div >
    <!-- {{this.empLeaveSummary}} -->
    <div id="showformlistid" v-show="showFormList">
      <div class="h-35 d-inline-block" style="width: 100%">
        <h5 class="align-left pad-left">Employee Leave transaction </h5>
      </div>
      <baseList
        :listColumns="listColumns"
        :totalRows="totalLHRows"
        v-on:searchToParent="fetchStartRec"
        @fetchD="fetchLeaveHistory"
        :items="leaveHistory"
        :moreDetails="moreDetails"
        :newButtonLabel="'Add new leave transaction'"
        :showDetailsButton="false"
        @OpenNew="NewPage"
        @OpenCurrent="getCurrentPage"
        @selected="selectedRow"
        :sortByColumn="moreDetails"
      />
    </div>
    <hr>
    <!-- <div>
      <leaveSummary :empData="this.empLeaveSummary" :msg="this.msg" />
    </div> -->
    <!-- <b-row class="mt-1">
      <div class="d-flex justify-content-start">
        <b-col offset="1" class="col-left">Loan Installment</b-col>
      </div>
    </b-row> -->
  </div>
</template>

<script>
import baseList from '../components/BaseList.vue'
// import leaveSummary from './LeaveSummary.vue'
import { mapActions, mapGetters } from 'vuex'
import axios from 'axios'
export default {
  data () {
    return {
      id: {},
      msg: {},
      empLeaveSummary: {},
      listColumns: {
        attr1: { name: 'empCode', label: 'Employee Code' },
        attr2: { name: 'id', label: 'Employee Name' },
        attr3: { name: 'dateFrom', label: 'From' },
        attr4: { name: 'dateTo', label: 'To' },
        attr5: { name: 'noOfWorkingdays', label: 'No. of days' },
        attr6: { name: 'applicationDate', label: 'Application Date' }
      },
      moreDetails: {
        attr1: { name: 'id', label: 'Transaction Id' },
        attr2: { name: 'approvalStatus', label: 'Status' },
        attr3: { name: 'leaveNature', label: 'Leave Nature' },
        attr4: { name: 'adjLeave', label: 'Leave Adjustment' },
        attr5: { name: '', label: '' },
        attr6: { name: '', label: '' }
      }
    }
  },
  created () {
    // console.log('Created ', '')
  },
  computed: {
    ...mapGetters([
      'leaveHistory',
      'showForm',
      'showFormList',
      'totalRows',
      'totalLHRows',
      'startRec',
      'leaveHistoryList'
    ])
  },
  components: {
    baseList
    // leaveSummary
  },
  methods: {
    ...mapActions([
      'fetchLeaveHistory',
      'setFormDisplay',
      'defaultState',
      'setIsEditMode',
      'fetchEmployee',
      'fetchStartRec'
    ]),
    getCurrentPage: function (x) {
      this.setFormDisplay(true)
      this.setIsEditMode(false)
      this.fetchEmployee(x)
    },
    NewPage: function () {
      this.$bvModal.show('bv-modal-addLeave')
    },
    selectedRow: function (x) {
      // alert(x)
      this.fetchEmployeeLeaveSummary(x)
    },
    async fetchEmployeeLeaveSummary (id) {
      // alert(id)
      const url = process.env.VUE_APP_API_URL + `/employee/leaveSummary/${id}`
      await axios
        .get(url)
        .then((response) => {
          return response
        })
        .then((response) => {
          this.empLeaveSummary = response.data
          this.msg = {}
        })
        .catch(error => {
          console.log(error.response.data)
          this.empLeaveSummary = { annualLeave: 0, casualLeave: 0, id: 0, sickLeave: 0 }
          this.msg = error.response.data.errorMessage
        })
    }
  }
}
</script>

